import AppDispatcher from '../Dispatcher/AppDispatcher';
import { IDeveloperToolsTab } from './../IDeveloperToolsTab';
import DeveloperToolsTab from './../Models/DeveloperToolsTab';

import DeveloperConsoleResizeAction from './../Actions/DeveloperConsoleResizeAction';
import DeveloperConsoleShowHideAction from './../Actions/DeveloperConsoleShowHideAction';
import RegisterTabAction from './../Actions/RegisterTabAction';

/**
 * Contains all actions related to the Developer Console
 */
export default class DeveloperToolsConsoleActionCreator {
  public static resizeConsole(height: number): void {
    AppDispatcher.instance.dispatch(new DeveloperConsoleResizeAction(height));
  }

  public static showHideConsole(show: boolean): void {
    AppDispatcher.instance.dispatch(new DeveloperConsoleShowHideAction(show));
  }

  public static registerDeveloperToolsTab(developerToolsTab: IDeveloperToolsTab): void {
    const tab: DeveloperToolsTab = new DeveloperToolsTab(developerToolsTab);
    AppDispatcher.instance.dispatch(new RegisterTabAction(tab));
  }
}
